"use client";

import { GetSaleById } from "@/libs/api";
import { Sale } from "@/types/type";
import { TicketPercent } from "lucide-react";
import { useParams } from "next/navigation";
import React, { useEffect, useState } from "react";
import toast from "react-hot-toast";

const SaleDetail = () => {
  const { id } = useParams();
  const [sale, setSale] = useState<Sale | null>(null);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const token = localStorage.getItem("token") || "";

  const fetchSaleDetail = async () => {
    setIsLoading(true);
    try {
      const res = await GetSaleById(token, id as string);
      if (res.con) {
        setSale(res.result);
      } else {
        toast.error(res.message);
      }
      setIsLoading(false);
    } catch (error: unknown) {
      const message =
        error instanceof Error ? error.message : "Failed to fetch sale";
      toast.error(message);
    } finally {
      setIsLoading(false);
    }
  };
  console.log("sale", sale);

  useEffect(() => {
    if (!id) return;
    fetchSaleDetail();
  }, [id]);

  return (
    <div className="w-full h-full p-4 mx-auto">
      <div className="flex items-start mt-2">
        <div className="text-3xl font-bold text-gray-800 flex items-center gap-2 whitespace-nowrap">
          <TicketPercent className="w-8 h-8 text-blue-600" />
          <h1>Voucher</h1>
        </div>
        <div className="flex-1 ml-4 bg-gradient-to-r from-blue-200 to-blue-400 rounded-md" />
        <Voucher data={saleData} />
      </div>
    </div>
  );
};

export default SaleDetail;
